import React from 'react';
import PropTypes from 'prop-types';

export default function PopUpConditions({visible, setVisible}) {
  return (
    <section className={visible ? 'popup-conditions' : 'popup-conditions popup-conditions--hidden'} onClick={() => setVisible(false)}>
      <div className="popup-conditions__block" onClick={(e) => e.stopPropagation()}>
        <h2 className="popup-conditions__title"> </h2>
        <button className="popup-conditions__close" type="button" onClick={() => setVisible(false)}/>
        <p className="popup-conditions__text">
                      
                      
                      
                      
                      
                      
                      
                      
                      
                      
                      
                      
                      
                      
        </p>
      </div>
    </section>
  );
}

PopUpConditions.propTypes = {
  visible: PropTypes.bool.isRequired,
  setVisible: PropTypes.func.isRequired,
};
