import React from 'react';
import PropTypes from 'prop-types';

export default function PopUpConfidentiality({visible, setVisible}) {
  return (
    <section className={visible ? 'popup-confidentiality' : 'popup-confidentiality popup-confidentiality--hidden'} onClick={() => setVisible(false)}>
      <div className="popup-confidentiality__block" onClick={(e) => e.stopPropagation()}>
        <h2 className="popup-confidentiality__title">Frontend Developer</h2>
        <button className="popup-confidentiality__close" type="button" onClick={() => setVisible(false)}/>
        <p className="popup-confidentiality__text">
                      
                      
                      
                      
                      
                      
                      
                      
                      
                      
                      
                      
                      
                      
        </p>
      </div>
    </section>
  );
}

PopUpConfidentiality.propTypes = {
  visible: PropTypes.bool.isRequired,
  setVisible: PropTypes.func.isRequired,
};
